export class JobSummaryReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'JobSummaryReport';
		this.viewId = 'JobSummaryReport';
		this.context_menu_name = $.i18n._( 'Job Summary' );
		this.navigation_label = $.i18n._( 'Saved Report' ) + ':';
		this.view_file = 'JobSummaryReportView.html';
		this.api = TTAPI.APIJobSummaryReport;
	}
}
